<template>
  <div class="sliderRange">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/sliderRangeExample"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">sliderRange 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import sliderRange from "cy_static/sliderRange"; 
&lt;/script&gt;
</xmp >
 
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;div id="myRange"&gt;&lt;/div&gt;
&lt;script type="text/javascript"&gt;
    import sliderRange from "cy_static/sliderRange"; 
    sliderRange({
      step : 5,
      to : 1000,
      elem : "#myRange",
      onStart : function(left) {
      },// 拖动中
      onMove : function(left) {
      },// 滚动中
      onScroll : function(left) {
      }, // 拖动结束
      onStop : function(left) {
      }
  });
&lt;/script&gt;
</xmp >

      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
            <h2 class="cy_h2">构造方法</h2>				
            <ul class="ul-props">
              <li>
                <p><a>sliderRange</a>(dom : selector|String, params : Object) : <a>Object</a></p>
						    <p>滑块范围选择；</p>                
                <table class="table-props border">
                  <tr>
                    <th colspan="5">params 参数</th>
                  </tr>
                  <tr>
                    <th style="width:20%">参数名</th>
                    <th style="width:20%">类型</th>
                    <th style="width:20%; ">默认值</th>
                    <th style="width:30%;">描述</th>
                    <th style="width:10%; " class="text-c">必需</th>
                  </tr>
									<tr>
										<td>value</td>
										<td class="atn">Number</td>
										<td>0</td>
										<td>当前值</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>from</td>
										<td class="atn">Number</td>
										<td>0</td>
										<td>起始值</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>to</td>
										<td class="atn">Number</td>
										<td>100</td>
										<td>终止值</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>step</td>
										<td class="atn">Number</td>
										<td>1</td>
										<td>滑动步长</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>min</td>
										<td class="atn">Number</td>
										<td>0</td>
										<td>最小值</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>max</td>
										<td class="atn">Number</td>
										<td>99999999999</td>
										<td>最大值</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>className</td>
										<td class="atn">String</td>
										<td>""</td>
										<td>样式名</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>labelTxt</td>
										<td class="atn">String</td>
										<td>"{0}元"</td>
										<td>显示文本{0}会替换为当前值</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>toFixed</td>
										<td class="atn">Function</td>
										<td style=";word-break:break-all;">function (value) {
    return parseFloat(parseFloat(value).toFixed(1));
}
</td>
										<td>标尺上的数字转换方法</td>
										<td class="text-c">N</td>
									</tr>
									<tr>
										<td>scale</td>
										<td class="atn">Array</td>
										<td>[0, 10, 20, 30]</td>
										<td>标尺</td>
										<td class="text-c">Y</td>
									</tr> 
                </table>
              </li>              
            </ul>
      </div>
      <div class="apiModule">
        <h2 class="cy_h2">实例方法</h2>				
            <ul class="ul-props">
              <li>
                <p><a>pause</a>() : <a>Void</a></p>
                <p>禁止滑动；</p>
              </li>
              <li>
                <p><a>play</a>() : <a>Void</a></p>
                <p>启动滑动；</p>
              </li>		
              <li>
                <p><a>setActivated</a>(isActivated) : <a>Void</a></p>
                <p>是否激活；</p>
              </li>		
              <li>
                <p><a>setValue</a>(value, isCeil:Boolen = undefined) : <a>Void</a></p>
                <p>设置值；isCeil向上(下)调整步长;undefined时不调整</p>
              </li>		
              <li>
                <p><a>getValue</a>() : <a>Void</a></p>
                <p>获取值；</p>
              </li>
              
              <li>
                <p><a>destroy</a>() : <a>Void</a></p>
                <p>销毁实例里所有事件；</p>
              </li>
            </ul>
      </div>
 
    </div> 
  </div>
</template>

<script>
import {trim} from "../../../assets/js/codeStyle";
import sliderRange from "cy_static/sliderRange";
export default {
  name: 'sliderRange',
  data () {
    return {
      msg: 'sliderRange',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;  
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
  }, 
  methods:{
  }
}
</script>  
<style scoped>
  .cy_example .iframeBox{position:absolute;top:48px;width:100%;bottom:0}
</style>



